

import { apiClient } from './client'
import type { LoginRequest, LoginResponse, User } from '../types/auth'

class AuthService {

  async login(credentials: LoginRequest): Promise<LoginResponse> {
    try {
      const response = await apiClient.post<LoginResponse['data']>('/auth/login', credentials)
      
      if (response.success && response.data) {
        
        if (typeof window !== 'undefined') {
          localStorage.setItem('authToken', response.data.token)
          localStorage.setItem(
            'adminUser',
            JSON.stringify({
              id: response.data.user.id,
              email: response.data.user.email,
              role: response.data.user.role,
              name: response.data.user.name,
            })
          )
          
         
          if (response.data.expiresIn) {
            const expirationTime = Date.now() + response.data.expiresIn * 1000
            localStorage.setItem('tokenExpiration', expirationTime.toString())
          }
        }

        return {
          success: true,
          data: response.data,
          message: response.message || 'Login successful',
        }
      }

      return {
        success: false,
        error: response.error || 'Login failed',
        message: response.message || response.error || 'Login failed',
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'An unexpected error occurred'
      return {
        success: false,
        error: errorMessage,
        message: errorMessage,
      }
    }
  }

  logout(): void {
    if (typeof window !== 'undefined') {
      localStorage.removeItem('authToken')
      localStorage.removeItem('adminUser')
      localStorage.removeItem('tokenExpiration')
    }
  }

  /**
   * Get current user from storage
   */
  getCurrentUser(): User | null {
    if (typeof window === 'undefined') return null
    
    const userStr = localStorage.getItem('adminUser')
    if (!userStr) return null

    try {
      return JSON.parse(userStr) as User
    } catch {
      return null
    }
  }

  /**
   * Get auth token from storage
   */
  getToken(): string | null {
    if (typeof window === 'undefined') return null
    return localStorage.getItem('authToken')
  }

  /**
   * Check if token is expired
   */
  isTokenExpired(): boolean {
    if (typeof window === 'undefined') return true
    
    const expiration = localStorage.getItem('tokenExpiration')
    if (!expiration) return false // No expiration set, assume valid
    
    return Date.now() > parseInt(expiration, 10)
  }

  /**
   * Verify token with backend (optional - for token validation)
   */
  async verifyToken(): Promise<boolean> {
    try {
      const response = await apiClient.get<{ valid: boolean }>('/auth/verify')
      return response.success && response.data?.valid === true
    } catch {
      return false
    }
  }
}

// Export singleton instance
export const authService = new AuthService()